<template>
    <div class="page">
        <layouts>
            <template #time>
                <times :time="time"/>
            </template>
            <template #left>
                <div class="area">
                    <titlebox :title=" '能源经营业务' "/>
                    <ol class="flex">
                        <li class="tag">
                            <span class="tag-num" >0</span>
                            <span class="tag-info">备电点(个)</span>
                        </li>
                        <li class="tag">
                            <span class="tag-num" >0</span>
                            <span class="tag-info">充电点(个)</span>
                        </li>
                        <li class="tag">
                            <span class="tag-num" >0</span>
                            <span class="tag-info">换电点(个)</span>
                        </li>
                        <li class="tag">
                            <span class="tag-num" >0</span>
                            <span class="tag-info">售电点(个)</span>
                        </li>
                        <li class="tag">
                            <span class="tag-num" >0</span>
                            <span class="tag-info">能源包(个)</span>
                        </li>
                    </ol>
                </div>
                <div class="area marg">
                    <titlebox :title=" '能源生产保障' "/>
                    <ol class="flex">
                        <li class="tag">
                            <span class="tag-num" >0</span>
                            <span class="tag-info">储能站(个)</span>
                        </li>
                        <li class="tag">
                            <span class="tag-num" >0</span>
                            <span class="tag-info">延寿站(个)</span>
                        </li>
                        <li class="tag">
                            <span class="tag-num" >0</span>
                            <span class="tag-info">蓄电池(AH)</span>
                        </li>
                    </ol>
                </div>
            </template>
            <template #right>
                <tablemap/>
            </template>
        </layouts>
    </div>
</template>

<script>
import times from "../../components/tableTime";
import layouts from "../../components/layouts";
import tablemap from "../../components/table_map";
import titlebox from "../../components/title";
export default {
    data() {
        return {
            time: "2019-06-14 10:48:53"
        };
    },
    components: {
        times,
        layouts,
        tablemap,
        titlebox
    },
    created() {},
    mounted() {},
    methods: {}
};
</script>

<style scoped lang='scss'>
.page {
    padding-top: 0.7%;
    // width: 1280px;
    min-height: 700px;
}
.area {
    background: #fff;
}
</style>

<style lang="scss" scoped>
.flex {
    overflow: hidden;
    li {
        float: left;
    }
}
.flex > li:nth-child(3n + 0) {
    border-right: none;
}
.tag {
    margin-top: 20px;
    border-right: 1px solid #e3e3e3;
    margin-bottom: 24px;
    cursor: pointer;
    width: 33%;
}
.tag span {
    display: block;
    text-align: center;
}
.tag-num {
    font-size: 26px;
    color: #0070c9;
    margin-bottom: 18px;
}
.marg{
    margin-top: 10px;
}
</style>



